<template>
  <div class="container">
    <div class="login-box">
      <!-- ÏôºÏ™Ω -->
      <div class="left">
        <div class="back" @click="goBack">
          <i class="fa-solid fa-chevron-left"></i>
          <p class="font">
            <a href="/">Back to Login</a>
          </p>
        </div>

        <h4>ÎπÑÎ∞ÄÎ≤àÌò∏ Ï∞æÍ∏∞</h4>
        <p>ÎπÑÎ∞ÄÎ≤àÌò∏Î•º Ï∞æÏïÑÎ≥¥ÏÑ∏Ïöî</p>

        <div class="log_input">
          <div class="passwordbox">
            <input type="text" v-model="email" placeholder="Email" />
          </div>
        </div>

        <button class="loginbox" @click="handleSubmit">Ï†úÏ∂ú</button>

        <!-- SNS Î°úÍ∑∏Ïù∏ Í≤ΩÍ≥ÑÏÑ† -->
        <div class="boundary_line">
          <p>Or login with</p>
        </div>

        <!-- SNS Î°úÍ∑∏Ïù∏ -->
        <div class="snsbox">
          <div class="facebook">
            <i class="fa-brands fa-facebook"></i>
          </div>
          <div class="google">
            <img
                width="20"
                height="20"
                src="https://img.icons8.com/color/48/google-logo.png"
                alt="google-logo"
            />
          </div>
          <div class="apple">
            <i class="fa-brands fa-apple"></i>
          </div>
        </div>
      </div>

      <!-- Ïò§Î•∏Ï™Ω Ïù¥ÎØ∏ÏßÄ -->
      <div class="right">
        <img
            v-for="(slide, i) in slides"
            :key="i"
            :src="slide"
            alt="hotel image"
            class="slide"
            :class="{ active: i === index }"
        />
        <div class="dots">
          <span
              v-for="(dot, i) in slides"
              :key="'dot-' + i"
              class="dot"
              :class="{ active: i === index }"
              @click="showSlide(i)"
          ></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import "@/assets/css/FindPassword.css";

export default {
  name: "FindPasswordPage",
  data() {
    return {
      email: "",
      index: 0,
      slides: [
        require("@/assets/img/img.jpg"),
        require("@/assets/img/img2.jpg"),
        require("@/assets/img/img3.jpg"),
      ],
      timer: null,
    };
  },
  methods: {
    async handleSubmit() {
      if (!this.email) {
        alert("Ïù¥Î©îÏùºÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
        return;
      }
      try {
        const response = await axios.post('/api/user/send-reset-code', {
          userEmail: this.email,
        });

        // üö® [ÏàòÏ†ïÎêú Î∂ÄÎ∂Ñ] üö®
        const responseData = response.data;
        const alertMessage = responseData.message || "ÎπÑÎ∞ÄÎ≤àÌò∏ Ïû¨ÏÑ§Ï†ï ÏΩîÎìúÍ∞Ä Î∞úÏÜ°ÎêòÏóàÏäµÎãàÎã§."; // messageÍ∞Ä ÏóÜÏúºÎ©¥ Í∏∞Î≥∏ Î©îÏãúÏßÄ ÏÇ¨Ïö©

        if (responseData.code === 'SUCCESS') {
          alert(alertMessage);
          this.$router.push(`/authenticate/${this.email}`);
        } else {
          // ERROR ÏΩîÎìúÍ∞Ä ÏôîÏùÑ ÎïåÎèÑ message ÌïÑÎìúÍ∞Ä ÏóÜÏúºÎ©¥ Í∏∞Î≥∏ Î©îÏãúÏßÄ ÏÇ¨Ïö©
          alert(alertMessage);
        }
      } catch (error) {
        console.error("ÎπÑÎ∞ÄÎ≤àÌò∏ Ï∞æÍ∏∞ Ïã§Ìå®:", error);
        if (error.response && error.response.data && error.response.data.message) {
          alert(error.response.data.message); // üö® HTTP Ïò§Î•ò ÏãúÏóêÎßå ÏûëÎèô
        } else {
          alert("ÎπÑÎ∞ÄÎ≤àÌò∏ Ï∞æÍ∏∞ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. ÏÑúÎ≤Ñ ÎòêÎäî ÎÑ§Ìä∏ÏõåÌÅ¨ ÏÉÅÌÉúÎ•º ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî."); // üö® Î∂àÏ∂©Î∂Ñ
        }
      }
    },
    goBack() {
      this.$router.push("");
    },
    showSlide(n) {
      this.index = n;
    },
    nextSlide() {
      this.index = (this.index + 1) % this.slides.length;
    },
  },
  mounted() {
    this.timer = setInterval(this.nextSlide, 3000);
  },
  beforeUnmount() {
    clearInterval(this.timer);
  },
};
</script>

