            </div>
          </div>
        </div>

        <div class="check_box">
          <label>
            <input type="checkbox" v-model="remember" /> Ï†ÄÏû•ÌïòÍ≥† One ÌÑ∞ÏπòÎ°ú Í≤∞Ï†úÌïòÍ∏∞
          </label>
        </div>

        <button class="Pay_Add" @click="handleSubmit">Í≤∞Ï†úÏàòÎã® Ï∂îÍ∞Ä</button>

        <p class="description">
          By confirming your subscription, you allow The Outdoor Inn Crowd Limited to
