<script>
export default {
  name: "PaymentMethod",
  data() {
    return {
      cardNumber: "",
      expDate: "",
      cvc: "",
      cardName: "",
      country: "",
      remember: false,
      images: [
        require("@/assets/img/img.jpg"),
        require("@/assets/img/img2.jpg"),
        require("@/assets/img/img3.jpg"),
      ],
      countries: [
        "ÎÑ§ÎçúÎûÄÎìú", "ÎÖ∏Î•¥Ïõ®Ïù¥", "Îâ¥ÏßàÎûúÎìú", "ÎåÄÌïúÎØºÍµ≠", "Îç¥ÎßàÌÅ¨",
        "ÎèÖÏùº", "Îü¨ÏãúÏïÑ", "Î©ïÏãúÏΩî", "ÎßêÎ†àÏù¥ÏãúÏïÑ", "ÎØ∏Íµ≠", "Î≤†Ìä∏ÎÇ®",
        "Î≤®Í∏∞Ïóê", "Î∏åÎùºÏßà", "Ïä§Ïõ®Îç¥", "Ïä§ÏúÑÏä§", "Ïä§ÌéòÏù∏", "Ïã±Í∞ÄÌè¨Î•¥",
        "ÏïÑÎ•¥Ìó®Ìã∞ÎÇò", "ÏòÅÍµ≠", "Ïò§Ïä§Ìä∏Î¶¨ÏïÑ", "Ïù¥ÌÉàÎ¶¨ÏïÑ", "Ïù∏ÎèÑ", "ÏùºÎ≥∏",
        "Ï∫êÎÇòÎã§", "ÏΩúÎ°¨ÎπÑÏïÑ", "Ïπ†Î†à", "Ï≤¥ÏΩî", "Ï§ëÍµ≠", "ÌÑ∞ÌÇ§", "ÌÉúÍµ≠",
        "ÌéòÎ£®", "Ìè¥ÎûÄÎìú", "ÌîÑÎûëÏä§", "ÌïÑÎ¶¨ÌïÄ", "ÌóùÍ∞ÄÎ¶¨", "Ìò∏Ï£º", "Í∑∏Î¶¨Ïä§",
        "ÎÇ®ÏïÑÌîÑÎ¶¨Ïπ¥Í≥µÌôîÍµ≠", "Ìè¨Î•¥Ìà¨Í∞à", "ÌïÄÎûÄÎìú"
      ],
      currentIndex: 0,
      intervalId: null,
    };
  },
  methods: {
    formatCardNumber() {
      let value = this.cardNumber.replace(/\D/g, ""); // Ïà´ÏûêÎßå
      value = value.substring(0, 16); // ÏµúÎåÄ 16ÏûêÎ¶¨
      this.cardNumber = value.replace(/(\d{4})(?=\d)/g, "$1 ");
    },
    handleSubmit() {
      const cNumber = this.cardNumber.replace(/\s/g, "").trim();
      const expDate = this.expDate.trim();
      const cvc = this.cvc.trim();
      const name = this.cardName.trim();
      const country = this.country.trim();

      if (!cNumber || !expDate || !cvc || !name || !country) {
        alert("Î™®Îì† Ìï≠Î™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
        return;
      }
      if (!/^\d{16}$/.test(cNumber)) {
        alert("Ïπ¥ÎìúÎ≤àÌò∏Îäî Ïà´Ïûê 16ÏûêÎ¶¨Ïó¨Ïïº Ìï©ÎãàÎã§.");
        return;
      }
      if (!/^\d{3}$/.test(cvc)) {
        alert("CVCÎäî Ïà´Ïûê 3ÏûêÎ¶¨Ïó¨Ïïº Ìï©ÎãàÎã§.");
        return;
      }
      if (!/^(0[1-9]|1[0-2])\/\d{2}$/.test(expDate)) {
        alert("Ïïå Ïàò ÏóÜÎäî ÎÇ†Ïßú ÌòïÏãùÏûÖÎãàÎã§. MM/YY ÌòïÏãùÏúºÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî. (Ïòà: 03/25)");
        return;
      }
      if (!/^[a-zA-Z\s]+$/.test(name)) {
        alert("Ïπ¥Îìú Ïù¥Î¶ÑÏùÄ ÏòÅÎ¨∏ÏúºÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
        return;
      }
      if (!this.remember) {
        alert("ÏïΩÍ¥ÄÏóê ÎèôÏùò Ìï¥Ï£ºÏÑ∏Ïöî.");
        return;
      }

      alert("ÌöåÏõêÍ∞ÄÏûÖÏù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.");
      this.$router.push("/");
    },
    showSlide(n) {
      this.currentIndex = n;
    },
    nextSlide() {
      this.currentIndex = (this.currentIndex + 1) % this.images.length;
    },
  },
  mounted() {
    this.intervalId = setInterval(this.nextSlide, 3000);
  },
  beforeUnmount() {
    clearInterval(this.intervalId);
  },
};
</script>

