<template>
  <div class="container">
    <div class="login-box">
      <div class="left">
        <div class="back" @click="goBack">
          <i class="fa-solid fa-chevron-left"></i>
          <p class="font" @click="$router.push('/')">Back to Login</p>
        </div>

        <h4>Ïù∏Ï¶ùÌïòÍ∏∞</h4>
        <p>Ïù¥Î©îÏùºÎ°ú Î∞õÏùÄ Ïù∏Ï¶ùÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî</p>

        <div class="log_input">
          <div class="codebox">
            <input
                :type="showPassword ? 'text' : 'password'"
                v-model="code"
                placeholder="code"
            />
            <i
                class="fa-solid"
                :class="showPassword ? 'fa-eye-slash' : 'fa-eye'"
                @click="togglePassword"
            ></i>
          </div>

          <button type="button" class="resend" @click="resendCode">
            Ïû¨Ï†ÑÏÜ°ÌïòÍ∏∞
          </button>
        </div>

        <button class="certified" @click="handleAuthenticate">Ïù∏Ï¶ùÌïòÍ∏∞</button>
      </div>

      <div class="right">
        <img
            v-for="(slide, i) in slides"
            :key="i"
            :src="slide"
            alt="hotel image"
            class="slide"
            :class="{ active: i === index }"
        />
        <div class="dots">
          <span
              v-for="(dot, i) in slides"
              :key="'dot-' + i"
              class="dot"
              :class="{ active: i === index }"
              @click="showSlide(i)"
          ></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import "@/assets/css/Authenticate.css";

export default {
  name: "AuthenticatePage",
  data() {
    return {
      code: "",
      showPassword: false,
      index: 0,
      slides: [
        require("@/assets/img/img.jpg"),
        require("@/assets/img/img2.jpg"),
        require("@/assets/img/img3.jpg"),
      ],
      timer: null,
      userEmail: this.$route.params.userEmail
    };
  },
  methods: {
    togglePassword() {
      this.showPassword = !this.showPassword;
    },
    async resendCode() {
      try {
        const response = await axios.post('/api/user/verify-reset-code', {
          userEmail: this.userEmail
        });

        if (response.data.code === 'SUCCESS') {
          alert("Ïù∏Ï¶ù ÏΩîÎìúÍ∞Ä Ïù¥Î©îÏùºÎ°ú Ïû¨Ï†ÑÏÜ°ÎêòÏóàÏäµÎãàÎã§. Î©îÏùºÏùÑ ÌôïÏù∏Ìï¥Ï£ºÏÑ∏Ïöî.");
        } else {
          alert(response.data.message);
        }
      } catch (error) {
        console.error("Ïù∏Ï¶ù ÏΩîÎìú Ïû¨Ï†ÑÏÜ° Ïã§Ìå®:", error);
        alert("Ïù∏Ï¶ù ÏΩîÎìú Ïû¨Ï†ÑÏÜ° Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Ïû†Ïãú ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.");
      }
    },
    goBack() {
      this.$router.push("/findPassword");
    },
    async handleAuthenticate() {
      if (!this.code.trim()) {
        alert("Ïù∏Ï¶ùÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
        return;
      }

      try {
        // ÏÉàÎ°ú Ï∂îÍ∞ÄÎêú ÎπÑÎ∞ÄÎ≤àÌò∏ Ïû¨ÏÑ§Ï†ï Ï†ÑÏö© APIÎ•º Ìò∏Ï∂úÌï©ÎãàÎã§.
        const response = await axios.post('/api/user/verify-reset-code', {
          userEmail: this.userEmail,
          verificationCode: this.code,
        });

        if (response.data.code === 'SUCCESS') {
          alert("Ïù¥Î©îÏùº Ïù∏Ï¶ùÏù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§. ÏÉàÎ°úÏö¥ ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏÑ§Ï†ïÌï¥Ï£ºÏÑ∏Ïöî.");

          // Ïù∏Ï¶ù ÏÑ±Í≥µ ÏãúÏóêÎßå Password_Setting ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
          this.$router.push({
            name: 'Password_Setting',
            params: {
              userEmail: this.userEmail,
              verificationCode: this.code
            }
          });
        } else {
          alert(response.data.message);
        }
      } catch (error) {
        console.error("Ïù∏Ï¶ùÎ≤àÌò∏ ÌôïÏù∏ Ïã§Ìå®:", error);
        if (error.response && error.response.data && error.response.data.message) {
          alert(error.response.data.message);
        } else {
          alert("Ïù∏Ï¶ù Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Ïû†Ïãú ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.");
        }
      }
    },
    showSlide(n) {
      this.index = n;
    },
    nextSlide() {
      this.index = (this.index + 1) % this.slides.length;
    },
  },
  mounted() {
    this.timer = setInterval(this.nextSlide, 3000);
  },
  beforeUnmount() {
    clearInterval(this.timer);
  },
};
</script>
<style scoped>
@import "@/assets/css/Authenticate.css";
